import Vue from "vue";
import VueRouter from "vue-router"
import NavHome from "../views/NavHome.vue";
import NavAddPost from "../views/NavAddPost.vue";
import NavAbout from "../views/NavAbout.vue"
import NavPost from "../views/NavPost.vue"
import NavEdit from "../views/NavEdit.vue"

Vue.use(VueRouter);

const routes = [
    {
        path: "/",
        name: "home",
        component: NavHome,
    },
    {
        path: "/about",
        name: "about",
        component: NavAbout,
    },
    {
        path: "/add-post",
        name: "add-post",
        component: NavAddPost,
    },
    {
        path: "/post/:id",
        name: "post",
        component: NavPost,
    },
    {
        path: "/edit-post/:id",
        name: "edit-post",
        component: NavEdit,
    },
]

const router = new VueRouter({
    routes
  })
  
export default router